@use 'abstracts/mixins' as *;

.jumbotron {
  padding-top: 166px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--white);
    font-size: 20px;
    margin-bottom: 23px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 21px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--white);
    font-weight: 300;
    margin-bottom: 98px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
      margin-bottom: 112px;
      padding-right: 10px;
    }

    span {
      color: var(--gold);
    }
  }

  &__description {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 85px;
    padding-right: 179px;

    @include md {
      padding-right: 9px;
      padding-bottom: 46px;
    }

    p {
      position: relative;
      max-width: 640px;
      width: 100%;
      color: var(--white);
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 1.2px;
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);

      @include md {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.8px;
        width: 100%;
        padding-left: 63px;
        padding-right: 5px;
      }

      &::before {
        content: '';
        display: block;
        background-color: var(--gold);
        width: 2px;
        margin-top: 2px;
        height: 857px;
        position: absolute;
        top: 0;
        left: -60px;

        @include md {
          height: 575px;
          left: 39px;
        }
      }
    }
  }

  &__parallax {
    background: url('./../assets/esg.webp');
    min-height: 400px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include md {
      min-height: 340px;
      background-attachment: scroll !important;
      background-position: center !important;
    }
  }
}

.intro {
  padding-top: 100px;

  @include md {
    padding-top: 46px;
  }

  .container {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }

  &__text {
    flex-shrink: 0;
    margin-right: 179px;
    max-width: 640px;
    padding-right: 12px;
    padding-left: 3px;
    width: 70%;
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: 300;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    margin-bottom: 28px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.4px;
      width: 100%;
      padding-left: 63px;
      margin-right: 0;
      padding-right: 0;
      margin-bottom: 22px;
    }
  }
}

.book {
  padding-bottom: 86px;
  padding-top: 93px;

  @include md {
    padding-top: 82px;
    padding-bottom: 60px;
  }

  .esg__title {
    margin-bottom: 18px;

    @include md {
      margin-bottom: 14px;
    }
  }

  &__text {
    flex-shrink: 0;
    max-width: 690px;
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: 300;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    margin-bottom: 58px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      padding-left: 63px;
      letter-spacing: 0.4px;
      margin-bottom: 38px;
    }
  }

  &__form {
    min-height: 575px;
    background: var(--light-grey);
    display: flex;
    flex-direction: column;

    &__content {
      flex-grow: 1;
      padding-top: 115px;
      display: flex;
      align-items: center;
      justify-content: center;

      @include md {
        padding-top: 112px;
        align-items: flex-start;
      }

      h5 {
        text-transform: uppercase;
        color: var(--mid-green);
        font-size: 22px;
        letter-spacing: 2.2px;

        @include md {
          font-size: 16px;
          letter-spacing: 1.6px;
        }
      }
    }

    &__button-container {
      padding-bottom: 61px;
      max-width: 340px;
      width: 100%;
      margin: 0 auto;

      @include md {
        max-width: 251px;
        padding-bottom: 52px;
      }

      .button-outlined {
        @include md {
          padding-left: 11px;
          font-size: 10px;
          letter-spacing: 1px;
          height: 39px;
        }
      }
    }
  }
}

// COMMON

.esg__title {
  text-transform: uppercase;
  font-size: 28px;
  letter-spacing: 2.8px;
  color: var(--mid-green);
  display: flex;
  align-items: center;
  padding-left: 37px;

  @include md {
    font-size: 16px;
    letter-spacing: 1.6px;
    padding-left: 0;
  }

  &::before {
    content: '';
    display: block;
    background: url('./../assets/title-logo.svg');
    background-size: cover;
    margin-right: 16px;
    position: relative;
    top: 5px;
    height: 72px;
    width: 72px;
    flex-shrink: 0;

    @include md {
      content: '';
      width: 41px;
      margin-right: 9px;
      height: 41px;
      top: -1px;
    }
  }
}

.section-container {
  padding-left: 193px;
  max-width: 1000px;

  @include md {
    padding-left: 0;
  }
}

.esg-companies {
  display: flex;
  flex-wrap: wrap;
  margin-right: 179px;
  max-width: 640px;
  width: 70%;
  margin-top: 50px;
  margin-bottom: 60px;
  align-items: center;
  justify-content: center;

  div {
    margin: 0 15px 40px 15px;
  }

  @include md {
    width: 100%;
    margin-right: 0;
    padding-right: 0;
    margin-bottom: 60px;

    div {
      width: 100%;
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
    }
  }
}

.esg-container {
  background-color: var(--white);
  background-image: url('./../assets/pricing-bg.webp');
  background-position: right -140px top 150px;
  background-size: 876px;
  background-repeat: no-repeat;

  @include md {
    background-size: 517px;
    background-position: right -270px top 270px;
  }
}
